.Menu {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  background-color: rgb(0, 0, 0, 0.8);
  cursor: pointer;
}

.MenuHeader, .MenuContent, .MenuFooter {
  color: white;
  padding-left: 10%;
  padding-right: 10%;
}

.btn {
  border: 2px solid black;
  background-color: rgb(0, 0, 0, 0);
  color: black;
  padding: 10px 24px;
  font-size: 16px;
  cursor: pointer;
  margin: 5px;
}

.default {
  border-color: #e7e7e7;
  color: white;
}

.default:hover {
  color: black;
  background: #e7e7e7;
}

.is-red {
  color: red;
}

.selected {
  color: black;
  background: #e7e7e7;
}

button::-moz-focus-inner {
  border: 0;
}



